.form {
  margin: 0rem 0;
  height: 18rem;
  overflow: auto;
}

.control {
  margin-bottom: 0.5rem;
}

.control label {
  font-weight: bold;
  margin-bottom: 0.25rem;
  display: block;
}

.control input {
  font: inherit;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 20rem;
  /* max-width: 100%; */
}

input.str {
  font: inherit;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 43.5rem;
  /* max-width: 100%; */
}

.actions {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
}

.actions button {
  font: inherit;
  color: #5a1a01;
  cursor: pointer;
  background-color: transparent;
  border: none;
  border-radius: 25px;
  padding: 0.5rem 2rem;
}

.actions button:hover,
.actions button:active {
  background-color: #ffe6dc;
}

.actions .submit {
  border: 1px solid #5a1a01;
  background-color: #5a1a01;
  color: white;
}

.actions .submit:hover,
.actions .submit:active {
  background-color: #7a2706;
}

.invalid label {
  color: #ca3e51;
}

.invalid input {
  border-color: #dd253d;
  background-color: #ffeff1;
}

p.invalid {
  color: rgba(235, 55, 55, 0.863);
  font-size: 14px;
}

.actions button:disabled,
.actions button:disabled:hover,
.actions button:disabled:active {
  background-color: rgb(180, 174, 174);
  color: #616161;
  border-color: rgb(180, 174, 174);
  cursor: not-allowed;
}

@media (max-width: 768px) {
  .control input {
    font: inherit;
    border: 1px solid #ccc;
    border-radius: 4px;
    width: 10rem;
    /* max-width: 100%; */
  }
}
